import { AfterViewInit, ChangeDetectorRef, ChangeDetectionStrategy, Component, ElementRef, EventEmitter, HostListener, Injector, NgModule, OnDestroy, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatSidenav } from '@angular/material';
import { Router, RouterModule } from '@angular/router';
import { MediaChange, ObservableMedia } from '@angular/flex-layout';
import { Subscription } from 'rxjs';

import { Util } from '../../util/util';
import { OSharedModule } from '../../shared';
import { InputConverter } from '../../decorators';
import { OAppSidenavImageModule } from './image/o-app-sidenav-image.component';
import { OAppSidenavMenuItemModule } from './menu-item/o-app-sidenav-menu-item.component';
import { OAppSidenavMenuGroupModule } from './menu-group/o-app-sidenav-menu-group.component';
import { OAppLayoutComponent, OAppLayoutMode, OSidenavMode } from '../../layouts/app-layout/o-app-layout.component';
import { AppMenuService, MenuRootItem, MenuItemUserInfo, MenuGroup, OUserInfoService, UserInfo } from '../../services';

export const DEFAULT_INPUTS_O_APP_SIDENAV = [
  'opened',
  'showUserInfo: show-user-info',
  'showToggleButton: show-toggle-button',
  'openedSidenavImg: opened-sidenav-image',
  'closedSidenavImg: closed-sidenav-image',
  'layoutMode: layout-mode',
  'sidenavMode: sidenav-mode'
];

export const DEFAULT_OUTPUTS_O_APP_SIDENAV = [
  'onSidenavToggle',
  'afterSidenavToggle'
];

@Component({
  moduleId: module.id,
  selector: 'o-app-sidenav',
  inputs: DEFAULT_INPUTS_O_APP_SIDENAV,
  outputs: DEFAULT_OUTPUTS_O_APP_SIDENAV,
  template: `
    <mat-sidenav-container class="o-app-sidenav-container" [class.opened]="sidenav.opened"
      [class.o-app-sidenav-mode-desktop]="(layoutMode || 'desktop') === 'desktop'" [class.o-app-sidenav-mode-mobile]="(layoutMode || 'desktop') !== 'desktop'">

      <mat-sidenav #sidenav class="o-app-sidenav-sidenav o-scroll" [opened]="isSidenavOpened()" [mode]="(sidenavMode || ((layoutMode || 'desktop') === 'desktop' ? 'side' : 'over'))"
        [class.mat-drawer-opened]="sidenav.opened" [class.mat-drawer-closed]="!sidenav.opened" fxLayout="column"
        fxLayoutAlign="start stretch">

        <div fxLayout="row" fxLayoutAlign="end" *ngIf="showToggleButton" class="sidenav-toggle-container">
          <mat-icon class="sidenav-toggle" (click)="toggleSidenav()" svgIcon="ontimize:menu">menu</mat-icon>
        </div>

        <o-app-sidenav-image [opened-src]="openedSidenavImg" [closed-src]="closedSidenavImg"> </o-app-sidenav-image>

        <ng-content select="o-app-layout-sidenav-projection-start"></ng-content>

        <nav *ngFor="let menuRootItem of menuRootArray">

          <ul *ngIf="appMenuService.getMenuItemType(menuRootItem) !== 'group'">
            <o-app-sidenav-menu-item [menu-item]="menuRootItem" [menu-item-type]="appMenuService.getMenuItemType(menuRootItem)"
              [sidenav-opened]="sidenav.opened" (onClick)="onMenuItemClick($event)">
            </o-app-sidenav-menu-item>
          </ul>

          <o-app-sidenav-menu-group [menu-group]="menuRootItem" [sidenav-opened]="sidenav.opened" *ngIf="appMenuService.getMenuItemType(menuRootItem) === 'group'"
            (onItemClick)="onMenuItemClick($event)">
          </o-app-sidenav-menu-group>
        </nav>

        <ng-content select="o-app-layout-sidenav-projection-end"></ng-content>

      </mat-sidenav>

      <mat-sidenav-content class="o-app-sidenav-content">
        <ng-content></ng-content>
      </mat-sidenav-content>
    </mat-sidenav-container>
  `,
  styles: [`
    .o-app-sidenav.header-layout .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav{box-shadow:3px 56px 6px rgba(0,0,0,0.24)}.o-app-sidenav .o-app-sidenav-menu-group[disabled=true] .mat-button-focus-overlay,.o-app-sidenav .o-app-sidenav-menu-group[disabled=true] .mat-ripple,.o-app-sidenav .o-app-sidenav-menu-item[disabled=true] .mat-button-focus-overlay,.o-app-sidenav .o-app-sidenav-menu-item[disabled=true] .mat-ripple{display:none}.o-app-sidenav .o-app-sidenav-menu-group[disabled=true] .o-app-sidenav-item,.o-app-sidenav .o-app-sidenav-menu-item[disabled=true] .o-app-sidenav-item{opacity:.5}.o-app-sidenav .o-app-sidenav-menu-group[disabled=true] a,.o-app-sidenav .o-app-sidenav-menu-item[disabled=true] a{cursor:initial}.o-app-sidenav .o-app-sidenav-container{width:100%;height:100%}.o-app-sidenav .o-app-sidenav-container.o-app-sidenav-mode-desktop:not(.opened) .o-app-sidenav-sidenav.mat-drawer-closed{visibility:visible !important;transform:translate3d(-191px, 0, 0)}.o-app-sidenav .o-app-sidenav-container.o-app-sidenav-mode-desktop:not(.opened) .o-app-sidenav-content.mat-drawer-content{margin-right:48px !important;transform:translate3d(48px, 0, 0) !important}.o-app-sidenav .o-app-sidenav-container .o-app-sidenav-content{position:absolute;right:0;left:0}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav{box-shadow:3px 0 6px rgba(0,0,0,0.24);padding-bottom:36px;padding-top:5px;width:240px;bottom:0;overflow:auto;height:100%}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav h3{border:none;font-size:16px;font-weight:700;line-height:24px;text-transform:capitalize;margin:0;padding-left:16px}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav ul{list-style-type:none;margin:0;padding:0}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav li{margin:0;padding:0}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav li>a{box-sizing:border-box;display:block;font-size:14px;font-weight:400;line-height:47px;text-decoration:none;padding:0 16px;position:relative}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav .sidenav-toggle{padding:4px 12px 8px;cursor:pointer}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav .o-app-sidenav-menugroup{transition:background-color 500ms}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav .o-app-sidenav-menugroup.active+.o-app-sidenav-menugroup-items-container .o-app-sidenav-menu-item:last-child li{border-color:transparent}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav .o-app-sidenav-menugroup-items-container{overflow:hidden}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closing{transform:translate3d(-191px, 0, 0)}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed{text-align:center}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed .o-app-sidenav-item:not(.o-user-info){cursor:pointer}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed .o-app-sidenav-item.o-user-info:focus,.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed .o-app-sidenav-item.o-user-info:hover{color:inherit}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed .o-app-sidenav-item.o-app-sidenav-menuitem a{width:48px;max-width:48px;min-width:48px;line-height:48px;padding:0 8px;height:48px}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed .o-app-sidenav-item.o-app-sidenav-menugroup{padding:8px 8px 8px 12px;height:48px;-webkit-box-pack:center !important;-ms-flex-pack:center !important;justify-content:center !important}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed>nav{width:48px;min-width:48px;margin-left:auto}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed .o-app-sidenav-image .o-app-sidenav-image-container{padding:0;text-align:end;max-height:48px}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav.mat-drawer-closed .o-app-sidenav-image .o-app-sidenav-image-container .o-app-sidenav-image{max-height:48px;margin:8px 8px 0 0}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav:not(.mat-drawer-closed) .o-app-sidenav-item{border-bottom-width:0;border-bottom-style:solid}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav:not(.mat-drawer-closed) .o-app-sidenav-menugroup{padding:16px;transition:padding 500ms 0s}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav:not(.mat-drawer-closed) .o-app-sidenav-menugroup .o-app-sidenav-menugroup-arrow{position:absolute;right:16px;transition:500ms transform cubic-bezier(0.25, 0.8, 0.25, 1)}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav:not(.mat-drawer-closed) .o-app-sidenav-menugroup.active .o-app-sidenav-menugroup-arrow{transform:rotate(90deg)}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav:not(.mat-drawer-closed) .o-app-sidenav-menugroup.active+.o-app-sidenav-menugroup-items-container ul li>a{padding-left:32px}.o-app-sidenav .o-app-sidenav-container .mat-drawer.o-app-sidenav-sidenav:not(.mat-drawer-closed) .o-app-sidenav-menuitem mat-icon{margin-right:16px}.o-app-sidenav .o-app-sidenav-container .o-app-sidenav-content{min-height:100%;display:flex;flex-direction:column}.o-app-sidenav .o-app-sidenav-container .o-app-sidenav-content router-outlet+*{flex-grow:1}
  `],
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-app-sidenav]': 'true'
  },
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OAppSidenavComponent implements OnInit, OnDestroy, AfterViewInit {

  public static DEFAULT_INPUTS_O_APP_LAYOUT = DEFAULT_INPUTS_O_APP_SIDENAV;
  public static DEFAULT_OUTPUTS_O_APP_LAYOUT = DEFAULT_OUTPUTS_O_APP_SIDENAV;

  @ViewChild(MatSidenav) sidenav: MatSidenav;

  protected routerSubscription: Subscription;
  appMenuService: AppMenuService;
  protected _menuRootArray: MenuRootItem[] = [];
  protected _layoutMode: OAppLayoutMode;
  protected _sidenavMode: OSidenavMode;
  @InputConverter()
  protected opened: boolean = true;
  _showUserInfo: boolean = true;
  _showToggleButton: boolean = true;
  openedSidenavImg: string;
  closedSidenavImg: string;

  onSidenavToggle: EventEmitter<boolean> = new EventEmitter<boolean>();
  afterSidenavToggle: EventEmitter<boolean> = new EventEmitter<boolean>();
  protected oUserInfoService: OUserInfoService;
  protected userInfoSubscription: Subscription;
  protected userInfo: UserInfo;

  protected mediaWatch: Subscription;
  protected manuallyClosed: boolean = false;

  constructor(
    protected injector: Injector,
    protected router: Router,
    protected elRef: ElementRef,
    protected cd: ChangeDetectorRef,
    protected media: ObservableMedia
  ) {
    this.appMenuService = this.injector.get(AppMenuService);
    this.menuRootArray = this.appMenuService.getMenuRoots();
    this.oUserInfoService = this.injector.get(OUserInfoService);
    const self = this;
    this.mediaWatch = this.media.subscribe((change: MediaChange) => {
      if (self.isScreenSmall() && self.sidenav) {
        self.sidenav.close();
      }
    });
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (!this.manuallyClosed && !this.isScreenSmall() && !this.isMobileMode()) {
      this.sidenav.open();
    }
  }

  ngOnInit() {
    this.routerSubscription = this.router.events.subscribe(() => {
      if (this.isScreenSmall()) {
        this.sidenav.close();
      }
    });
  }

  ngAfterViewInit() {
    if (this.showUserInfo && this.showToggleButton) {
      this.userInfo = this.oUserInfoService.getUserInfo();
      this.userInfoSubscription = this.oUserInfoService.getUserInfoObservable().subscribe(res => {
        this.userInfo = res;
        this.refreshMenuRoots();
      });
    }
    this.refreshMenuRoots();
  }

  get layoutMode(): OAppLayoutMode {
    return this._layoutMode;
  }

  set layoutMode(val: OAppLayoutMode) {
    let m = OAppLayoutComponent.OAppLayoutModes.find(e => e === val);
    if (Util.isDefined(m)) {
      this._layoutMode = m;
    }
  }

  get sidenavMode(): OSidenavMode {
    return this._sidenavMode;
  }

  set sidenavMode(val: OSidenavMode) {
    let m = OAppLayoutComponent.OSidenavModes.find(e => e === val);
    if (Util.isDefined(m)) {
      this._sidenavMode = m;
    }
  }

  protected refreshMenuRoots() {
    if (this.showUserInfo && this.userInfo && this._showToggleButton) {
      const firstRoot = this.menuRootArray[0];
      const alreadyExistsUserInfo = firstRoot ? firstRoot.id === 'user-info' : false;
      if (alreadyExistsUserInfo) {
        const userInfoItem: MenuItemUserInfo = (this.menuRootArray[0] as MenuGroup).items[0] as MenuItemUserInfo;
        userInfoItem.id = this.userInfo.username;
        userInfoItem.name = this.userInfo.username;
        userInfoItem.user = this.userInfo.username;
        userInfoItem.avatar = this.userInfo.avatar;
      } else {
        const userInfoItem: MenuItemUserInfo = {
          id: this.userInfo.username,
          name: this.userInfo.username,
          user: this.userInfo.username,
          avatar: this.userInfo.avatar
        };
        const menuGroupUserInfo: MenuGroup = {
          id: 'user-info',
          name: 'APP_LAYOUT.USER_PROFILE',
          items: [userInfoItem],
          opened: true,
          icon: 'person_pin'
        };
        this.menuRootArray.unshift(menuGroupUserInfo);
      }
    }
  }

  ngOnDestroy() {
    if (this.routerSubscription) {
      this.routerSubscription.unsubscribe();
    }
    if (this.userInfoSubscription) {
      this.userInfoSubscription.unsubscribe();
    }
  }

  isScreenSmall(): boolean {
    return !this.manuallyClosed && this.media.isActive('lt-sm');
  }

  isMobileMode(): boolean {
    return this._layoutMode === 'mobile';
  }

  isSidenavOpened(): boolean {
    return this.opened && !this.isMobileMode() && !this.isScreenSmall();
  }

  get menuRootArray(): MenuRootItem[] {
    return this._menuRootArray;
  }

  set menuRootArray(val: MenuRootItem[]) {
    this._menuRootArray = val;
  }

  toggleSidenav() {
    const promise = this.sidenav.opened ? this.sidenav.close() : this.sidenav.open();
    const self = this;
    promise.then(() => {
      self.afterSidenavToggle.emit(self.sidenav.opened);
    });
    this.cd.detectChanges();
    this.opened = this.sidenav.opened;
    this.manuallyClosed = !this.opened;
    this.onSidenavToggle.emit(this.sidenav.opened);
  }

  get showUserInfo(): boolean {
    return this._showUserInfo;
  }

  set showUserInfo(arg: boolean) {
    this._showUserInfo = arg;
  }

  get showToggleButton(): boolean {
    return this._showToggleButton;
  }

  set showToggleButton(arg: boolean) {
    this._showToggleButton = arg;
  }

  onMenuItemClick(e: Event): void {
    if (this.isMobileMode()) {
      this.sidenav.close();
    }
  }

}

@NgModule({
  imports: [CommonModule, OAppSidenavMenuGroupModule, OAppSidenavImageModule, OAppSidenavMenuItemModule, OSharedModule, RouterModule],
  declarations: [OAppSidenavComponent],
  exports: [OAppSidenavComponent]
})
export class OAppSidenavModule { }
